html, body{
    height: 100vh;
    width: 100vw;
    margin: 0;

    display: grid;
    grid-template-rows: 1fr 9fr;
}

#header{
    grid-area: 1fr;
}

#gameboard{
    display: grid;
    grid-template-columns: 1fr 1fr 1fr 1fr;
    grid-template-rows: 1fr 1fr 1fr;
    grid-area: 1fr;
    height: 100%;
    width: 100%;


}

.card{
    border: solid;
    grid-area: 1fr / 1fr ;
    height: 200px;
    width: auto;
    overflow: hidden;
}

.card > img{
    height: 200px;
    width: auto;
}